import { useState } from 'react'
import './App.css';
const notesWithRelativeMinors = {
  'C': 'A',
  'G': 'E',
  'D': 'B',
  'A': 'F♯',
  'E': 'C♯',
  'B': 'G♯',
  'F♯': 'D♯',
  'C♯': 'A♯',
  'G♭': 'E♭',
  'D♭': 'B♭',
  'A♭': 'F',
  'E♭': 'C',
  'B♭': 'G',
  'F': 'D',
}
const majorKeyCircle = Object.keys(notesWithRelativeMinors)
const minorKeyCircle = Object.values(notesWithRelativeMinors)

const noteQuality = ['♭', '♯']
const chordType = ['m', 'maj']
const extensions = ['7', '9']
const inversions = ['root position', '1st inversion', '2nd inversion']
const notes = ['A', 'B', 'C', 'D', 'E', 'F', 'G']
const piano = 
  [].concat(...notes.map((note) => {
    if (note === 'B' || note === 'E') {
      return [`${note}${noteQuality[0]}`,`${note}`]
    }
    return [`${note}${noteQuality[0]}`,`${note}` , `${note}${noteQuality[1]}`]
  })).filter((key) => !['F♭', 'C♭', 'B♯', 'E♯'].includes(key))

const getThird = (root, quality, piano) => {
  const semiTones = quality === 'maj' ? 4 : 3
    const pianoLength = piano.length -1
    const notePositionDifference = pianoLength - piano.indexOf(root)
   
    if (notePositionDifference < semiTones) {
      return piano[semiTones-notePositionDifference - 1]
    }
  return piano[piano.indexOf(root) + semiTones]
  
}
const getFith = (root, keyCircle) => {
  const indexOfRoot = keyCircle.indexOf(root)
  if (indexOfRoot !== keyCircle.length - 1) {
    return keyCircle[indexOfRoot + 1]
  }
  if(root === 'D♯') {
    console.log(keyCircle, keyCircle(0))
  }
  return keyCircle[0]
}

const filterPiano = (root, piano) => {
  if (root.includes('♭')) {
    return piano.filter((key) => !key.includes('♯'))
  }
    return piano.filter((key) => !key.includes('♭'))

}
const createChordList = () => {
  const majorChords = majorKeyCircle.reduce((acc, majorNote) => {
    return {...acc, [majorNote]: {
      quality: 'maj',
      root: majorNote,
      fifth: getFith(majorNote, majorKeyCircle),
      third: getThird(majorNote, 'maj', filterPiano(majorNote, piano))
    }}
  }, {})
  const minorChords = minorKeyCircle.reduce((acc, minorNote) => {
    return {...acc, [minorNote]: {
      quality: 'm',
      root: minorNote,
      fifth: getFith(minorNote, minorKeyCircle),
      third: getThird(minorNote, 'm', filterPiano(minorNote, piano))
    }}
  }, {})
  return  {...minorChords, ...majorChords}
}

const calcChordNotes = (chordObj, inversion) => {
  const { root, fifth, third } = chordObj
  if (inversion === 0) {
    return `${root} ${third} ${fifth}`
  }
  if(inversion === 1) {
    return `${third} ${fifth} ${root}`
  }
  if(inversion === 2) {
    return `${fifth} ${root} ${third}`
  }
}
const generateChord = (chordList, inversion) => {
  const keys = Object.keys(chordList)
  const randomRoot = keys[Math.floor(Math.random() * keys.length)]
  return {
    chord: `${randomRoot}${chordList[randomRoot].quality}`,
    notes: calcChordNotes(chordList[randomRoot], inversion)
  }
}


function App() {
  const [chord, setChord] = useState({chord: 'Cmaj', notes: 'C E G'})
  const [chordlist, setChordList] = useState(createChordList())
  const [inversion, setInversion] = useState(0)
  const [showChordKeys, setShowChordKeys] = useState(false)

  const onClick = async () => {
    
    const newInversion = Math.floor(Math.random()* inversions.length)
    await setInversion(newInversion)
    setChord(generateChord(chordlist, newInversion))
    setShowChordKeys(false)
  }
  return (
    <div className="App">
      <div className="navbar">
        <h1>Chord generator</h1>
      </div>
      <div className="container">
        <h2 className="chord">{chord.chord}</h2>
        <p className="inversion">{inversions[inversion]}</p>
        <button className="generate" onClick={() => onClick()}>New chord</button>
        {!showChordKeys ? <button className='generate' onClick={() => {setShowChordKeys(true)}}>Show answer</button>
        :<p>notes: {chord.notes}</p>
  }</div>
    </div>
  );
}

export default App;
